import axios from "axios";
import { createContext, useEffect, useState } from "react";

export let CartContext = createContext();
export function CartContextProvider(props) {
    let baseURL = "https://route-ecommerce.onrender.com"
    let headers = {
        token: localStorage.getItem("token"),
    }
    let [cartDeta, setcartDeta] = useState()
    useEffect(()=>{
        getAllCartDetails()
    },[])
    async function getAllCartDetails() {
        let { data } = await axios.get(`${baseURL}/api/v1/cart`, { headers: headers })
        console.log(data)
        setcartDeta(data)
    }
    async function removeItem(id) {
        let { data } = await axios.delete(`${baseURL}/api/v1/cart/${id}`, { headers: headers })
        setcartDeta(data)
    }
    async function updateQuatity(id,count) {
        let body ={
            count:count
        }
        let { data } = await axios.put(`${baseURL}/api/v1/cart/${id}`,body, { headers: headers })
        setcartDeta(data)
    }
    return <CartContext.Provider value={{ getAllCartDetails, cartDeta, removeItem, updateQuatity }}>
        {props.children}
    </CartContext.Provider>
}