import React, { useContext } from 'react'
import { Link, NavLink } from 'react-router-dom'
import Logo from "../../assetes/freshcart-logo.svg"
import { CartContext } from '../../Context/CartContext'
export default function Navbar({ userdata, Logout }) {
  let { cartDeta, removeItem, updateQuatity } = useContext(CartContext)
 
  return <>
    <nav className="navbar navbar-expand-lg bg-light">
      <div className="container">
        <Link className="navbar-brand" to="/"><img src={Logo} className="w-100" alt="" /></Link>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          {userdata ? <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink className={({ isActive }) => isActive ? "nav-link active" : "nav-link"} to="home">Home</NavLink>
            </li>
          </ul>
            : ""}


          <ul className="navbar-nav ms-auto mb-2 mb-lg-0">

            {userdata ? <>
              <li className="nav-item py-2">
                <i className='fa-brands fa-instagram mx-2'></i>
                <i className='fa-brands fa-facebook mx-2'></i>
                <i className='fa-brands fa-tiktok mx-2'></i>
                <i className='fa-brands fa-twitter mx-2'></i>
                <i className='fa-brands fa-linkedin mx-2'></i>
                <i className='fa-brands fa-youtube mx-2'></i>
              </li>
              <li className="nav-item">
                <NavLink className={({ isActive }) => isActive ? "nav-link active" : "nav-link"} to="profile">Profile</NavLink>
              </li>
              <li className="nav-item" data-bs-toggle="offcanvas" data-bs-target="#offcanvasRight" aria-controls="offcanvasRight">
                <span className="nav-link">
                  <div className="position-relative">
                    <i className='fa-solid fa-shopping-cart  fa-lg'></i>
                    <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-btn">
                      {cartDeta?.numOfCartItems}
                    </span>
                  </div>

                </span>
              </li>
              <li className="nav-item">
                <span className='nav-link cursor-pointer' onClick={Logout}>LogOut</span>
              </li>
            </> : <>
              <li className="nav-item">
                <NavLink className={({ isActive }) => isActive ? "nav-link active" : "nav-link"} to="login">Login</NavLink>
              </li>

              <li className="nav-item">
                <NavLink className={({ isActive }) => isActive ? "nav-link active" : "nav-link"} to="/">Register</NavLink>
              </li>
            </>}

          </ul>

        </div>
      </div>
    </nav>

    <div className="offcanvas offcanvas-end" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel">
      <div className="offcanvas-header">
        <h5 className="offcanvas-title" id="offcanvasRightLabel">Cart Details</h5>
        <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
      </div>
      <div className="offcanvas-body">
        {cartDeta ?
          cartDeta.data.products.map((el) => {
            if (el.count== 0) {
              removeItem(el.product._id)
            } 
            return <>
            <div className='border-bottom border-2 py-2'>
              <div className='d-flex justify-content-between align-items-center'>
                <img src={el.product.imageCover} className='w-25' height={100} alt="" />
                <div>
                  <button onClick={() => updateQuatity(el.product._id, el.count -= 1)} className='btn bg-btn btn-sm  px-2 rounded'>-</button>
                  <span className='mx-3'>{el.count}</span>
                  <button onClick={() => updateQuatity(el.product._id, el.count += 1)} className='btn bg-btn btn-sm  px-2 rounded'>+</button>
                  
                </div>
                <div>
                  <i onClick={()=>removeItem(el.product._id)} className='fa-solid fa-trash text-danger cursor-pointer'> </i>
                </div>
              </div>
              <h5>{el.product.title}</h5>
            </div> 
            </>
          })


          : ""}

      </div>
      {cartDeta ?
      <div className='offcanves-bottom m-auto mb-2'>
      <Link to="/home" className="btn btn-dark text-white w-100 mb-3">Add more items</Link>
      <Link to={"/checkout/"+cartDeta.data._id} className="btn bg-btn text-white w-100 ">CheckOut Payment</Link>
      </div> :""}
    </div>


  </>
}
