import axios from 'axios';
import { useFormik } from 'formik'
import React from 'react'
import { useParams } from 'react-router-dom';

export default function Checkout() {
      let baseURL = "https://route-ecommerce.onrender.com"
      let headers = {
        token:localStorage.getItem("token"),
      }
      let {cardId} = useParams()
    let formik = useFormik({
        initialValues: {
            details: "",
            phone: "",
            city: ""
        },
        onSubmit: (val) => {
            checkOut(val,cardId);
        }
    })
   async function checkOut(val,id){
    let body ={
        shippingAddress:val
    }
let {data}= await axios.post(`${baseURL}/api/v1/orders/checkout-session/${id}?url=http://localhost:4200`,body,{headers})
if(data.status == "success"){
    window.open(data.session.url)
}
    }
    return (
        <div>
            <form onSubmit={formik.handleSubmit}>
                <div className='my-2'>
                    <label htmlFor="details">details</label>
                    <input onChange={formik.handleChange} type="text" name="details" id="details" className='form-control' />
                </div>
                <div className='my-2'>
                    <label htmlFor="phone">phone</label>
                    <input onChange={formik.handleChange} type="text" name="phone" id="phone" className='form-control' />
                </div>
                <div className='my-2'>
                    <label htmlFor="city">city</label>
                    <input onChange={formik.handleChange} type="text" name="city" id="city" className='form-control' />
                </div>
                <button type='submit' className='btn bg-btn text-white'>Pay</button>
            </form>
        </div>
    )
}
