import React, { useEffect } from 'react'
import { useContext } from 'react'
import { CartContext } from '../../Context/CartContext'
import $ from "jquery"
import { Link } from 'react-router-dom'
export default function CartDetails() {
    let { getAllCartDetails ,cartDeta, removeItem, updateQuatity} = useContext(CartContext)
    useEffect(() => {
        getAllCartDetails()
        $(".Loading").fadeOut(3000)
    }
        , [])
    return <>
    <div className='position-fixed top-0 end-0 start-0 bottom-0 bg-white Loading justify-content-center align-items-center'>
  <span className="loader"></span>
  </div>
        <div>
            {cartDeta?  
          <>   
          <table style={{ verticalAlign: 'middle' }} className='table  table-bordered my-3 text-center'>
            <thead>
                    <tr>
                        <th>image</th>
                        <th>name</th>
                        <th>quatity</th>
                        <th>Price</th>
                        <th>Action</th>
                    </tr>
                </thead>
                <tbody>
          { cartDeta.data.products.map((el)=>{
            if (el.count== 0) {removeItem(el.product._id)} 
           return <tr>
                        <td>
                            <img src={el.product.imageCover} className='w-75' height={100} alt="" />
                        </td>
                        <td>{el.product.title}</td>
                        <td>
                            <button onClick={()=>updateQuatity(el.product._id,el.count-=1)} className='btn bg-btn btn-sm  px-2 rounded'>-</button>
                            <span className='mx-3'>{el.count}</span>
                            <button onClick={()=>updateQuatity(el.product._id,el.count+=1)} className='btn bg-btn btn-sm  px-2 rounded'>+</button>
                        </td>
                        <td>{el.price}EGP</td>
                        <td>
                        <i onClick={()=>removeItem(el.product._id)} className='fa-solid fa-trash text-danger cursor-pointer'> </i>
                        </td>
                    </tr>})}
                    <tr className='table-danger'>
                        <td colSpan={4}>Total</td>
                        <td>{cartDeta.data.totalCartPrice}EGP</td>
                    </tr>
                </tbody>
            </table>
            <Link to={"/checkout/"+cartDeta.data._id} className="btn bg-btn text-white">CheckOut Payment</Link>
            </>
            :""}
           
        </div>
    </>

}
